<template>
	<view class="container">
		<view class="cu-bar bg-transparent search">
			<view class="cu-avatar round ">
				<image :src="descTotal.userAvatar" mode="widthFix" class="round"></image>
			</view>
			<view class="text-left padding">
				{{descTotal.userNickName}}
			</view>
			<view class="action">
				<view class="padding-xs">
					<view class='cu-tag round light' :class="followed==true?'bg-grey':'bg-red'">{{follow}}</view>
				</view>
			</view>
		</view>
		<!-- 左右切换 -->
		<scroll-view scroll-x class="nav text-center nav_pos" :scroll-left="scrollLeft">
			<view class="cu-item" :class="index==TabCur?'text-blue text-bold currentLine cur':''"
				v-for="(item,index) in tabNav" :key="index" @tap="tabSelect" :data-id="index">
				{{item.name}}
			</view>
		</scroll-view>
		<swiper :current="TabCur" @change="SwiperChange" :style="{ height:mainHeight + 'px' }">
			<!-- 这个是tabNav的 根据tabNav的长度进行下面的循环 -->
			<swiper-item class="swiperItem" v-for="(dayData, i) in dataList" :key='i'>
				<view class="wrap m-1 pt-2 zerotimeline-class">
					<ZeroTimeline ref="zeroTimeline" :dataList='dayData' gap='30px' :show-left="true">
					</ZeroTimeline>
				</view>
			</swiper-item>
		</swiper>
		<!-- 攻略 -->

		<view style="padding: 40rpx;">
			<!-- 标题 -->
			<h2>{{descTotal.strategyTitle}}·<text style="color:blue">{{descTotal.day}}天</text></h2>
			<!-- 主体内容 -->
			<view style="padding-top: 20rpx;line-height: 1.5;">{{descTotal.strategyDesc}}
			</view>
			<!-- 话题 -->
			<view style="display: flex;justify-content: space-between;" v-for="(item,index) in descTotal.topicList"
				:key="index">
				<view class="" style="color: blue;margin-bottom: 20rpx;margin-top: 20rpx;">#{{item.topicName}} </view>
			</view>
			<!-- 点赞 收藏帖子 收藏地点 -->
			<view class="flex justify-between" style="margin-top: 20rpx;">
				<view class="action" @click="likePost">
					<view class="padding-xs">
						<view class='cu-tag round light' :class="liked==true?'bg-grey':'bg-purple'">
							<view class="iconfont icon-shoucang" style="padding: 10rpx;font-size: 25rpx;">{{like}}
							</view>
						</view>
					</view>
				</view>

				<view class="action" @click="collectPost">
					<view class="padding-xs">
						<view class='cu-tag round light' :class="collected==true?'bg-grey':'bg-purple'">
							<view class="iconfont icon-shoucang" style="padding: 10rpx;font-size: 25rpx;">{{collect}}
							</view>
						</view>
					</view>
				</view>


				<!-- 				<view class="action" @click="collectPlace">
					<view class="padding-xs">
						<view class='cu-tag round light' :class="placeCollected==true?'bg-grey':'bg-purple'">
							<view class="iconfont icon-didian" style="padding: 10rpx;font-size: 25rpx;">{{store}}
							</view>
						</view>
					</view>
				</view> -->



			</view>
		</view>
	</view>
	</view>
</template>

<script>
	import ZeroTimeline from "@/components/zero-timeline/zero-timeline.vue"
	export default {
		components: {
			ZeroTimeline
		},
		data() {
			return {
				collected: false,
				collect: "收藏帖子",
				liked: false,
				like: "已喜欢",
				TabCur: 0,
				scrollLeft: 0,
				mainHeight: 400, //全屏或者局部滑动设置的高度
				tabNav: [],
				list: [], //中间数组
				descTotal: {},
				dataList: [],
				strategyId: 0,
				desc: {
					userAvatar: "https://gd-hbimg.huaban.com/71cdd3cc1126caa5b4f346640ade2a58c275517a117083-zgsUvQ_fw236",
					userNickName: "Extrovert",
					stragetyTitle: "五一去杭州玩的攻略",
					stragetyDesc: "计划五一假期去杭州旅行是一个非常好的选择，因为杭州不仅有美丽的自然风光，还有丰富的文化遗产。下面是一个为期四天的杭州旅游攻略，涵盖了杭州的主要景点和一些实用的旅行小贴士。",
					topicList: [{
							topicName: "拍照",
						},
						{
							topicName: "打卡",
						},
						{
							topicName: "五一",
						}
					],
					publishTime: "2024.4.24"

				},
				follow: "关注",
				followed: false,
				collected: false,
				collect: 11,
				liked: false,
				like: 11,
				days: 4
			}
		},
		onLoad(options) {
			console.log(options.id)
			uni.request({
				url: 'http://110.40.182.65:8080/strategy/' + options.id,
				method: 'GET',
				data: {},
				success: res => {
					this.descTotal = res.data.data
					// 该用户是否被本用户关注
					this.followed = this.descTotal.followed
					this.collected = this.descTotal.collected
					this.liked = this.descTotal.liked
					// console.log(this.swiperList);
					if (this.followed) {
						this.follow = "已关注"
					} else {
						this.follow = "关注ta"
					}
					if (this.collected) {
						this.collect = "已收藏"
					} else {
						this.collect = "收藏攻略"
					}
					if (this.liked) {
						this.like = "已喜欢"
					} else {
						this.like = "喜欢"
					}
					this.descTotal.publishTime = this.dayjs(this.descTotal.publishTime).format(
						"YYYY-MM-DD HH:mm:ss");
					this.strategyId = this.descTotal.strategyId
					console.log(this.descTotal.publishTime)
					this.days = this.descTotal.day
					console.log(this.days)
					for (let i = 0; i < this.days; i++) {
						this.tabNav.push({
							name: '第' + (i + 1) + '天' // 修改为从“第1天”开始而不是“第0天”
						});
					}
					this.dataList = []
					this.dataList = this.descTotal.strategySets.map(day => [{
							title: day.morning,
							content: day.morningDesc,
							sub: '补充内容',
							leftTime: '上午',
							leftTitle: '上午',
							leftContent: day.morningPlayTime + '小时',
							color: this.getRandomColor(),
						},
						{
							title: day.lunch,
							content: day.lunchDesc,
							sub: '补充内容',
							leftTime: '中午',
							leftTitle: '午餐',
							leftContent: day.lunchTime + '小时',
							color: this.getRandomColor(),
						},
						{
							title: day.afternoon,
							content: day.afternoonDesc,
							sub: '补充内容',
							leftTime: '下午',
							leftTitle: '下午',
							leftContent: day.afternoonPlayTime + '小时',
							color: this.getRandomColor(),
						},
						{
							title: day.dinner,
							content: day.dinnerDesc,
							sub: '补充内容',
							leftTime: '晚上',
							leftTitle: '晚餐',
							leftContent: day.dinnerTime + '小时',
							color: this.getRandomColor(),
						},
						{
							title: day.night,
							content: day.nightDesc,
							sub: '补充内容',
							leftTime: '深夜',
							leftTitle: '晚上',
							leftContent: day.nightPlayTime + '小时',
							color: this.getRandomColor(),
						}
					])
				},
				fail: () => {},
				complete: () => {}
			});

			this.init()
			console.log(this.dataList)
			// 首次加载先将notice数组赋值给中间数组,否则刚打开页面是没有内容显示的
			this.list = this.notice
			this.tabCur = []

		},
		mounted() {
			this.updateMainHeight();
		},
		methods: {
			updateMainHeight() {
				this.$nextTick(() => {
					const query = uni.createSelectorQuery().in(this);
					query.select('.zerotimeline-class').boundingClientRect(rect => {
						if (rect) {
							this.mainHeight = rect.height
							this.mainHeight = this.mainHeight
						}
					}).exec();
				});
			},
			tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
				// 根据当前TabCur的值判断把哪个数组赋值给list
				this.list = this.TabCur == 0 ? this.notice : this.TabCur == 1 ? this.policy : this.news
				// Math.max(0, index - 1);
			},
			SwiperChange(e) {
				this.TabCur = e.detail.current
				this.scrollLeft = (e.detail.current - 1) * 60
				this.list = this.TabCur == 0 ? this.notice : this.TabCur == 1 ? this.policy : this.news
			},
			init() {
				// for (let i = 0; i < 5; i++) {
				// 	const obj = {
				// 		// time: `2023-11-1${i}`,
				// 		title: `南京总统府景区`,
				// 		content: `完成功能完成功能完成功能完成功能`,
				// 		sub: `补充内容`,
				// 		leftTime: `2023-11-1${i}`,
				// 		leftTitle: `11月1${i}日`,
				// 		leftContent: `开始\n|\n结束`,
				// 		color: this.getRandomColor(),

				// 	}
				// 	this.dataList.push(obj)
				// }

			},
			getRandomColor() {
				const red = Math.floor(Math.random() * 256);
				const green = Math.floor(Math.random() * 256);
				const blue = Math.floor(Math.random() * 256);
				return `rgb(${red}, ${green}, ${blue})`;
			},
			followUser() {
				//原本是关注，要取消关注
				if (this.followed == true) {
					uni.showModal({
						title: '确定要取消关注吗？',
						showCancel: true,
						cancelText: '取消',
						confirmText: '确定',
						success: res => {
							console.log(res);
							if (res.confirm == true) {
								//取消关注
								uni.request({
									url: 'http://110.40.182.65:8080/user/cancel/follow/' + this.userId,
									method: 'PUT',
									data: {},
									success: res => {
										console.log(res);
									}
								});
								this.followed = !this.followed
								this.follow = "关注ta"
							}
						},
						fail: () => {},
						complete: () => {}
					});
				} else {
					//原本没关注，关注
					uni.request({
						url: 'http://110.40.182.65:8080/user/follow/' + this.userId,
						method: 'PUT',
						data: {},
						success: res => {
							console.log(res);
						},
						fail: () => {
							uni.showToast({
								title: '关注失败',
								icon: "error"
							});
						},
						complete: () => {}
					});
					this.followed = !this.followed
					this.follow = "已关注"
				}
			},
			likePost() {
				//原本是关注，要取消关注
				if (this.liked == true) {
					uni.showModal({
						title: '确定要取消喜欢吗？',
						showCancel: true,
						cancelText: '取消',
						confirmText: '确定',
						success: res => {
							console.log(res);
							if (res.confirm == true) {
								//取消收藏
								uni.request({
									url: 'http://110.40.182.65:8080/strategy/cancel/' + this.strategyId +
										'/like',
									method: 'PUT',
									data: {},
									success: res => {
										console.log(res);
									}
								});
								this.liked = !this.liked
								this.like = "喜欢"
							}
						},
						fail: () => {},
						complete: () => {}
					});
				} else {
					//原本没关注，关注
					uni.request({
						url: 'http://110.40.182.65:8080/strategy/' + this.strategyId + '/like',
						method: 'PUT',
						data: {},
						success: res => {
							console.log(res);
						}
					});
					this.liked = !this.liked
					this.like = "已喜欢"
				}

			},
			collectPost() {
				//原本是关注，要取消关注
				if (this.collected == true) {
					uni.showModal({
						title: '确定要取消收藏吗？',
						showCancel: true,
						cancelText: '取消',
						confirmText: '确定',
						success: res => {
							console.log(res);
							if (res.confirm == true) {
								//取消收藏
								uni.request({
									url: 'http://110.40.182.65:8080/strategy/cancel/' + this.strategyId +
										'/collect',
									method: 'PUT',
									data: {},
									success: res => {
										console.log(res);
									}
								});
								this.collected = !this.collected
								this.collect = "收藏帖子"
							}
						},
						fail: () => {},
						complete: () => {}
					});
				} else {
					//原本没关注，关注
					uni.request({
						url: 'http://110.40.182.65:8080/strategy/' + this.strategyId + '/collect',
						method: 'PUT',
						data: {},
						success: res => {
							console.log(res);
						}
					});
					this.collected = !this.collected
					this.collect = "已收藏"
				}
			},
		}
	}
</script>

<style>
	@import '@/colorui/main.css';
	@import '@/colorui/icon.css';

	.tower-swiper .tower-item {
		transform: scale(calc(0.5 + var(--index) / 10));
		margin-left: calc(var(--left) * 100upx - 150upx);
		z-index: var(--index);
	}

	.container {
		margin-left: 15px;
		margin-right: 15px
	}
</style>